@import url(https://fonts.googleapis.com/css2?family=Zen+Kaku+Gothic+Antique:wght@400;700&display=swap);
#application-header {
  background-color: #323232;
  box-sizing: border-box;
  padding: 1rem;
}

#application-header h1 {
  color: #DDDDDD;
  font-size: 1.5rem;
  margin: 0 auto;
  padding: 0;
}

.product {
  border-bottom: 2px solid #323232;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  width: 100%;
  max-width: 700px;
}

.product:last-child {
  border-bottom: none;
}

.product .preview {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 0;
  margin-right: 2rem;
  width: 250px;
}

.product .preview img {
  height: 100px;
}

.product .info {
  width: 390px;
}

.product .info footer {
  align-items: center;
  display: flex;
  justify-content: flex-start;
}

.product .product_controller {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  padding: .5rem;
  justify-content: flex-start;
  width: 170px;
}

.product .user figure {
  border-radius: 100px;
  height: 40px;
  margin: 0;
  margin-right: .5rem;
  overflow: hidden;
  width: 40px;
}

.product .upvote button {
  background: transparent;
  border: none;
  cursor: pointer;
  margin-right: .5rem;
}

#application-footer {
    text-align: center;
    background-color: black;
    box-sizing: border-box;
    padding: 1rem;
  }
  
  #application-footer small {
    color: #FFFFFF;
  }
body {
  font-family: 'Zen Kaku Gothic Antique', sans-serif;
}

.product-list {
  margin: 0;
  width: 50%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

